@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

.nav-items {
    display: flex;
    margin-left: auto;
}

.nav-items ul {
    list-style: none;
    display: flex;
    margin-left: auto;
}

.nav-items li {
    margin-right: 25px;
    display: flex;
    justify-content: center;
}

.theme-changer {
    display: flex;
    margin-left: 25px;
}

.caption-header {
    font-family: 'Pacifico', cursive;
    font-size: 24px;
    margin-right: auto;
}

.nav-link {
    text-decoration: none;
    color: inherit;
}